<template>
    <el-main>
      <el-row>
        <el-col :span="24">
 <el-button style="float:left" type="primary" icon="el-icon-back" @click="goback()"></el-button>
          <el-button style="float:left"  type="primary" icon="el-icon-refresh" @click="init()"></el-button>
          <div class="demonstration h_title">Run SQL {{this.errorMessage}}</div>
        <el-button type="primary" @click="comfirmRunSql()">{{getlan('Execute')}}</el-button>
        </el-col>
      </el-row>
	<el-input type="textarea" :autosize="{ minRows: 3}" placeholder="sqlString" v-model="sqlString"></el-input>
      <el-table :data="tableData"   height="500"  style="font-size: 14px">

    <af-table-column
    v-for="(item,key) in tableData[0]"  :key="key"
    :prop="key"  :label="key"     >
  </af-table-column>
        

        <el-table-column :label="getlan('operate')" width="260">
          <template slot-scope="scope">
             <el-button size="small"   icon="el-icon-plus" @click="handleAdd(scope.$index, scope.row)"></el-button>
             <el-button size="small"   icon="el-icon-edit" @click="handleData(scope.$index, scope.row)"></el-button>
             <el-button size="small" icon="el-icon-delete"  @click="handleDelete(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>

<div class="block">
            <span class="demonstration"></span>
            <el-pagination 
                layout="prev, next" 
                @size-change="handleSizeChange"
                @current-change="current_change"
                :page-sizes="[50, 100, 500]"
                :page-size="this.pagesize"
                 :total="this.total">
            </el-pagination>
        </div>


    </el-main>
</template>

<script>
import Qs from 'qs'
    import{Type,AddData,DeleteData,GetList} from "@/api/index"
     import{getDay,getDateTime,getReqUrl,getLang,getValue,setValue} from "@/utils/pub"
    export default {
        name: 'App',
        data() {
        
            return {
              pageid: 1,
            pagesize: 50,
              loadFinish:0,
              total:0,
          tableData:[],
              CategoryName:'',
            errorMessage:'',
             dialogServer:false,
              belong:this.$route.params.belong,
                 tablename:this.$route.params.tablename,
                  sqlString:'',
              form:{
                 
                table:'runsql',
             
              },
               querymap:{ 
               pageid:this.pageid,
                pagesize:this.pagesize,
                table:'database',
              },
              rules:{
               DataBasename:[
                  { required: true, message: 'Please input name', trigger: 'change' },
                  { min: 1, max: 30, message: 'length 1 - 30', trigger: 'change' }
                ],
              }
            }
        },
        created(){
          this.belong=this.$route.params.belong;
          this.tablename=this.$route.params.tablename;
          var dbtype=getValue("dbtype")
          if(dbtype=="mssql"){
               this.sqlString ="select top 50  * from "+this.tablename +" "
          }else{
              this.sqlString ="select * from "+this.tablename +" limit 50 "
          }
        
           this.runsql()

        },
         methods: {
          getlan(name){ var str=this.$t('lang.'+name+''); if(str.indexOf("lang.")>=0){ return name; }return str },
          init(){


          },
           goback(){
	           this.$router.push({name:'tables'});
          },
            showtable(index, row){
               this.$router.push({name: 'tables', params: {dbname: row.dbname}})
            },
           handleAdd(index, row){
            this.errorMessage="";
            this.sqlString="insert into "+this.tablename+" ( "
            var columnstr=""
             var valuestr=""
             for(let key in row){
               columnstr+=key+","
              valuestr+= "'"+row[key]+"',"
             }
              this.sqlString+=columnstr.substring(0,columnstr.length-1)+")  values  ( "+valuestr.substring(0,valuestr.length-1)+") "
              this.form = row;
         
              this.dialogServer = true;
            
          },
          handleData(index, row){
            this.errorMessage="";
            this.sqlString="update "+this.tablename+" set "
           for(let key in row){
          this.sqlString+=" "+key+"='"+row[key]+"',"
          }
          this.sqlString=this.sqlString.substring(0,this.sqlString.length-1)
              this.sqlString+="  where xx=xx "
              this.form = row;
              this.form.mode='edit'
              this.dialogServer = true;
            
          },
           handleDelete(index, row) {
              this.sqlString="delete from  "+this.tablename+" where xx=xxx "
              this.dialogServer = true;
          },
          handleClose(formName){
            this.dialogServer = false;
            this.form = {
              id:0,
                DataBasename:'',

            }
            this.$refs['form'].resetFields();
          },//分页
        current_change:function(pageid){
          this.alert_ok(pageid)
            this.pageid = pageid;
        },
        handleSizeChange(val) {
            this.pagesize = val;
            
        },
         // 自定义label内容过滤器
        customLabel(str) {
          let ret = ''
          for (const char of str) {
            // 例：滤掉空格
            if (char !== ' '){
              ret += char
            }
          }
          return ret
        },
           // 自定义表头列宽
        flexColumnWidth(str) {
          let flexWidth = 0
        for (const char of str) {
            if ((char >= 'A' && char <= 'Z') || (char >= 'a' && char <= 'z')) {
              // 如果是英文字符，为字符分配8个单位宽度
              flexWidth += 8
            } else if (char >= '\u4e00' && char <= '\u9fa5') {
              // 如果是中文字符，为字符分配20个单位宽度
              flexWidth += 20
            } else {
              // 其他种类字符，为字符分配5个单位宽度
              flexWidth += 5
            }
          }
         flexWidth= str.length*16
          if (flexWidth < 50) {
            // 设置最小宽度
            flexWidth = 50
          }
          if (flexWidth > 250) {
            // 设置最大宽度
            flexWidth = 250
          }
          //console.log("str="+str+" width="+flexWidth +"px"+" length="+str.length +"px")
          return flexWidth
        },
          comfirmRunSql(){
         
    this.$confirm( this.sqlString,  this.getlan('Execute'), {
              confirmButtonText:  this.getlan('Execute'),
              confirmButtonClass: 'el-button--danger',
              cancelButtonText:  this.getlan('cancel'),
              type: 'warning'
            }).then(() => {
                this.runsql()
            }).catch(() => {});
          },
         
          runsql() {
         
          if(this.sqlString==''){
             this.alert_ok('sql is null')
             return
            }
             
          var  params={ 
              sql:this.sqlString,
              table:'runsql'
              }
                
              //console.log("this.sql="+this.form.sqlString)
                this.errorMessage="";
            AddData(Qs.stringify(params)).then(res=>{
             
              if(res.data.code==1){
                this.dialogServer = false;
                this.tableData=res.data.data;
                this.total=this.tableData.length;
                //this.$message({message:res.data.message,type: 'success'});
              }else{
                   if(res.data.message!=null){
                     this.errorMessage=res.data.message
                     this.alert_ok(res.data.message)
                   }
              }
            })

          }
        }
    }
</script>

<style lang="scss" scoped>
  .el-main {
    margin: 0px 0 0 160px;
    padding: 10px;
    .el-col {
      text-align: right;
    }
    .el-form--label-top .el-form-item__label {
      padding: 0;
    }
    .el-button.add {
      background-color: #4caf50;
      border: 0;
      font-size: 12px;
    }
     .h_title{
      margin-left:20px;
      float: left;
      line-height: 40px;
      font{
        color: #4caf50;
      }
    }
  }
</style>



